import { useEffect, useRef, useState } from "react";
import * as S from "./WriteModal.style";
import { dbService, storageService } from "firebase.js";
import { v4 as uuidv4 } from "uuid";
import firebase from "firebase";
import { userMiddleware } from "store/modules/userLike";
import { useDispatch, useSelector } from "react-redux";
import UpdateModal from "./UpdateModal";
import { useMediaQuery } from "react-responsive";
import { useHistory, useLocation } from "react-router";
import Loading from "../../Loading/Loading";

export default function WriteModal({ visible, isVisible, postData }) {
  const auth = firebase.auth();
  const isHeight = useMediaQuery({ maxHeight: 765 });

  const dispatch = useDispatch();
  const history = useHistory();
  const location = useLocation();

  const [post, setPost] = useState("");
  const [title, setTitle] = useState("");
  const [region, setRegion] = useState("");
  const [attachment, setAttachment] = useState([]);
  const [load, setLoad] = useState(false);
  const [login, setLogin] = useState("");

  const postRef = useRef();
  const titleRef = useRef();

  const onChange = (e) => {
    const { value, name } = e.target;
    if (name === "textarea") {
      setPost(value);
    } else if (name === "region") {
      setRegion(value);
    } else if (name === "title") {
      setTitle(value);
    }
  };
  // //    post post_id (users collection )
  let likePost = useSelector((state) => state.userLike.data);
  // //    post post_id (users collection )
  let bookmark = useSelector((state) => state.userLike.data);

  const onSubmit = async (e) => {
    e.preventDefault();
    if (!title) {
      alert("  .");
      postRef.current.focus();
    } else if (!post) {
      alert("  .");
      titleRef.current.focus();
    } else if (!region) {
      alert("  .");
    } else {
      setLoad(true);
      let attachmentUrl = [];
      if (attachment) {
        for (let i = 0; i < attachment.length; i++) {
          const attachmentRef = storageService
            .ref()
            .child(`${login.uid}/${uuidv4()}`);
          const response = await attachmentRef.putString(
            attachment[i],
            "data_url"
          );
          attachmentUrl.push(await response.ref.getDownloadURL());
        }
      }

      const ID = login.uid;
      const uuid = uuidv4();

      // users collection user_write_posts post_id 
      await dbService
        .collection("users")
        .doc(ID)
        .update({
          user_write_posts: firebase.firestore.FieldValue.arrayUnion(uuid),
        });

      //  

      const writeData = {
        post_title: title,
        post_content: post,
        post_writer: likePost.name || login.displayName,
        post_uid: login.uid,
        post_date: Date.now(),
        post_id: uuid,
        post_photo: attachmentUrl,
        post_profile_img: likePost.user_image || login.photoURL,
        post_region: region,
        post_view: 0,
        post_like: 0,
        uid: login.uid,
        post_update: false,
      };

      await dbService.collection("post").doc(uuid).set(writeData);
      setPost("");
      setTitle("");
      setRegion("");
      setAttachment([]);
      isVisible();
      setLoad(false);
      if (location.pathname === `/categorylist/${region}`) {
        //  
        history.push({
          pathname: `/categorylist/${region}`,
          search: `?id=${uuid}`,
          state: {
            like: likePost.user_like_posts,
            bookmark: bookmark.user_bookmark_posts,
            postData,
            write: 'write',
          },
        });
        window.location.reload();
      } else {
        history.push({
          pathname: `/categorylist/${region}`,
          state: { uuid },
        });
      }
    }
  };
  //   
  const onFileChange = (e) => {
    const { files } = e.target;
    let file;
    let fileURLs = [];

    for (let i = 0; i < files.length; i++) {
      file = files[i];
      let reader = new FileReader();
      reader.onload = () => {
        fileURLs[i] = reader.result;
        setAttachment([...fileURLs]);
      };
      reader.readAsDataURL(file);
    }
  };

  const onOverlayClick = (e) => {
    if (e.target !== e.currentTarget) {
      return;
    }
    closeModal();
  };

  // 
  const closeModal = () => {
    const ok = window.confirm(
      "    .  ?"
    );
    if (ok) {
      setPost("");
      setTitle("");
      setRegion("");
      setAttachment([]);
      isVisible();
    }
  };

  //   
  const removeAttachment = (e) => {
    setAttachment(attachment.filter((at) => at !== e));
  };

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      setLogin(user);
      dispatch(userMiddleware(user.uid, "", "init"));
    });
  }, []);

  return (
    <>
      {postData ? (
        <UpdateModal
          visible={visible}
          isVisible={isVisible}
          postData={postData}
          login={login}
          isHeight={isHeight}
          likePost={likePost}
        />
      ) : (
        <>
          <S.Overlay visible={visible} onClick={(e) => onOverlayClick(e)}>
            <S.Container visible={visible} isHeight={isHeight}>
              <S.CloseModal onClick={closeModal} className="fas fa-times" />
              {likePost && (
                <S.Wrapper>
                  {login.photoURL || likePost.user_image ? (
                    <>
                      <img
                        src={likePost.user_image || login.photoURL}
                        alt=" "
                      ></img>
                      <S.Name>{likePost.name || login.displayName}</S.Name>
                    </>
                  ) : (
                    <>
                      <S.NamelessIcon className="fas fa-user-circle" />
                      <S.Name>{likePost.name || login.displayName}</S.Name>
                    </>
                  )}
                </S.Wrapper>
              )}
              <form onSubmit={onSubmit}>
                <S.TitleInput
                  value={title}
                  name="title"
                  type="text"
                  tabIndex="1"
                  ref={titleRef}
                  onChange={onChange}
                  placeholder="  ."
                />
                <textarea
                  value={post}
                  name="textarea"
                  ref={postRef}
                  tabIndex="2"
                  onChange={onChange}
                  placeholder=" ."
                  rows="10"
                />
                <select
                  name="region"
                  value={region}
                  onChange={onChange}
                  tabIndex="3"
                >
                  <option value="">  .</option>
                  <option value="asia">Asia</option>
                  <option value="europe">Europe</option>
                  <option value="north_america">North America</option>
                  <option value="south_america">South America</option>
                  <option value="africa">Africa</option>
                  <option value="oceania">oceania</option>
                  <option value="antarctica">Antarctica</option>
                </select>
                <S.ImgUpload>
                  <label for="inputFile" tabIndex="4">
                     
                  </label>
                  <p> ctrl     .</p>
                  <input
                    multiple
                    id="inputFile"
                    accept="image/*"
                    type="file"
                    onChange={onFileChange}
                    name="fileNames[]"
                  />
                </S.ImgUpload>
                <S.ImgWrapper attachment={attachment}>
                  {attachment &&
                    attachment.map((atta, i) => (
                      <div>
                        <i
                          title="  "
                          onClick={() => removeAttachment(atta)}
                          className="fas fa-times"
                        />
                        <img key={i} src={atta} alt=" " />
                      </div>
                    ))}
                </S.ImgWrapper>
                {load ? (
                  <Loading width="30" height="30" />
                ) : (
                  <S.BtnWrapper>
                    <input
                      type="button"
                      tabIndex="5"
                      onClick={closeModal}
                      value=""
                    />
                    <input type="submit" tabIndex="6" value="" />
                  </S.BtnWrapper>
                )}
              </form>
            </S.Container>
          </S.Overlay>
        </>
      )}
    </>
  );
}
