import React, { useState, useRef } from 'react';
import * as S from "./Info.style";
import { useDispatch } from 'react-redux';
import { userMiddleware } from 'store/modules/userLike';
import { editMypageThunk } from 'store/modules/mypageComment';
import { editUserImgThunk, editUserNameThunk } from 'store/modules/mypagePost';
import { dbService, storageService } from 'firebase.js';
import { v4 as uuidv4 } from "uuid";
import Loading from "../../Loading/Loading";

const Info = ({ uid, user, userDB, change, setChange }) => {


  const input = useRef();

  const dispatch = useDispatch();

  const [attachment, setAttachment] = useState([]);

  let [load, setLoad] = useState(false);

  let [nickName, setNickName] = useState(false);

  let [img, setImg] = useState(false);



  const onUsername = (value) => {
    dbService.collection('users').doc(user.uid).update({
      name: value,      
    });
    dispatch(userMiddleware(user.uid, '', 'init'));
    dispatch(editMypageThunk(user.uid, value, 'name'));
    dispatch(editUserNameThunk(user.uid, value));
    setNickName(false);
    setChange(!change);
    setTimeout(()=>{
      dispatch(editMypageThunk(user.uid, '', 'finish'));
    }, 1000);
  };


  const onFileChange = (e) => {
    const { files } = e.target;
    let file;
    let fileURLs = [];

    file = files[0];
    let reader = new FileReader();
    reader.onload = () => {
      fileURLs[0] = reader.result;
      setAttachment([...fileURLs]);
    };
    reader.readAsDataURL(file);
  };

  const onEnter = (e) => {
    if(e.key != 'Enter'){
      return;
    }
    if(e.key === 'Enter'){
      e.preventDefault();
      onSubmitBtn();
    }
  };

  const onSubmitBtn = () => {
    onUsername(input.current.value);
    input.current.value = '';
  };

  const onSubmit = async(e) => {
    e.preventDefault();
    setLoad(true);
    let attachmentUrl = [];
    if (attachment) {
      const attachmentRef = storageService
        .ref()
        .child(`${uid.uid}/${uuidv4()}`);
      const response = await attachmentRef.putString(
        attachment[0],
        "data_url"
      );
      attachmentUrl.push(await response.ref.getDownloadURL());
    };

    await dbService.collection('users').doc(user.uid).update({
      user_image: attachmentUrl,
    });
    changeImg(attachmentUrl);
  };

  const onDefaultImg = async() => {
    await dbService.collection('users').doc(user.uid).update({
      user_image: null,
    });
    changeImg(null);
  }

  const changeImg = (data) => {
    setAttachment([]);
    dispatch(userMiddleware(user.uid, '', 'init'));
    dispatch(editUserImgThunk(user.uid, data));
    dispatch(editMypageThunk(user.uid, data, 'img'));
    setChange(!change);
    setImg(false);
    setTimeout(()=>{
      dispatch(editMypageThunk(user.uid, '', 'finish'));
    }, 1000);
    setLoad(false);
  }

  const onIconClick = () => {
    setNickName(!nickName);
  }

  const onImgClick = () => {
    setImg(!img);
  }

  return (
    <>
      <S.Id>
        <S.Paragraph>
          <S.Title></S.Title>
        </S.Paragraph>
        <p>{user.email}</p>
      </S.Id>
      <S.NickName>
        <S.Paragraph>
          <S.Title></S.Title>
          <S.Icon onClick={onIconClick} className="fas fa-cog" title={""}></S.Icon>
        </S.Paragraph>
        {nickName ?
        (<div>
          <textarea ref={input} type="text" maxLength="15" wrap="off" placeholder=" 15( )" onKeyPress={e=>onEnter(e)} />
          <button onClick={onSubmitBtn}></button>
        </div>) :
        (userDB.name ? <p>{userDB.name}</p> : <p onClick={onUsername}> !</p>)}
      </S.NickName>
      <S.Profile>
        <S.Paragraph>
          <S.Title> </S.Title>
          <S.Icon onClick={onImgClick} className="fas fa-cog" title={""}></S.Icon>
        </S.Paragraph>
        <S.Paragraph>
          {userDB.user_image ? (img || <p><S.ProfileImg src={userDB.user_image} alt=" "></S.ProfileImg></p>) : (img || <p><S.ProfileIcon className="fas fa-user-circle"></S.ProfileIcon></p>)}
          {img && <p>
            <S.Form onSubmit={onSubmit} img={userDB.user_image}>
              <div>
                <label for="inputFile"> </label>
                <input
                  id="inputFile"
                  accept="image/*"
                  type="file"
                  onChange={onFileChange}
                  name="fileNames[]"
                />
                {userDB.user_image &&
                <p onClick={onDefaultImg}> </p>}
              </div>
              {attachment && (
                <div>
                  {attachment.map((atta, i) => (
                    <>
                      <img key={i} src={atta} width="80px" height="80px" alt=" "/>
                      <button type="submit"> </button>
                    </>
                  ))}
                </div>
              )}
              {load && <S.Loading>
                <Loading width="30" height="30" />
              </S.Loading>}
            </S.Form>
          </p>}
        </S.Paragraph>
      </S.Profile>
    </>
  )
}

export default Info;